let interstitialAd = null
export default {
	data() {
		return {
		}
	},
	onLoad() {
		// 在页面onLoad回调事件中创建插屏广告实例
		if (wx.createInterstitialAd) {
		  interstitialAd = wx.createInterstitialAd({
		    adUnitId: 'adunit-3d758662f2d260e2'
		  })
		  interstitialAd.onLoad(() => {})
		  interstitialAd.onError((err) => {})
		  interstitialAd.onClose(() => {})
		}
	},
	onTabItemTap() {
		// 在适合的场景显示插屏广告
		if (interstitialAd) {
		  interstitialAd.show().catch((err) => {
		    console.error(err)
		  })
		}
	},
	methods: {
		// 前往硬币皮肤设置
		toCoinSkin () {
			uni.navigateTo({
				url: '/pages/setting/coinskin'
			});
		},
		// 前往硬币音效设置
		toCoinAudio () {
			uni.navigateTo({
				url: '/pages/setting/coinaudio'
			});
		},
		// 前往背景皮肤设置
		toCoinBg () {
			uni.navigateTo({
				url: '/pages/setting/bgCover'
			});
		},
		// 清除记录
		clearRecord () {
			uni.showModal({
			    title: '',
			    content: '清空我的统计记录，重新开始',
				confirmText: '清空',
				cancelText: '不清空',
				confirmColor: '#fd746c',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
						// uni.clearStorage();
						uni.setStorageSync('recordTotalCount', 0);
						uni.setStorageSync('recordFacadeCount', 0);
						uni.setStorageSync('recordReverseCount', 0);
						uni.showToast({
							title: '已清空',
							duration: 2000
						});
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
}
